html {
box-sizing: border-box;
font-size: 16px;
line-height: 1.5;
display:block;

@include responsive(small-phone) {
font-size: .9rem;
}
@include responsive(phone) {
font-size:  .9rem;
}
@include responsive(tablet) {
font-size: 1rem;
}
@include responsive(laptop) {
font-size: 1rem;
}
@include responsive(desktop) {
font-size: 1rem;
}
@include responsive(big-desktop) {
font-size: 1rem;
}

} // end html


body {
font-family: $font-family-reset;
color: $color-text;
font-weight: 400;
line-height: 1.5;
background-color: $color-primary;
background-image: linear-gradient(180deg, rgba($mogb, .3), rgba($mogb, .1)), url();
//$bh-blue-1: blue;
////background-color: #0183cb;
//background: #0694cb;
//$bh-blue-2:#181c57ff;

//$bh-blue-3:#000d5d;
//$df-blue-1:#011a28;
$mogb:#4C5D73;
$vligb:#90B0D9 ;
$gwh:#ECEDEC ;
// reds
//$dr-red-1:#800000ff;


overflow-x:hidden;
box-sizing:border-box;
} // end body

.wrapper {
display:grid;
width:100%;
grid-template-columns: repeat(12, [col-start] 1fr);
grid-template-rows:auto;

// .wrapper > * {
// grid-column: col-start / span 12;

// }

& > .sticky {
//position: fixed;
position: sticky;
//left: 0;
//right: 0;
top: 0;
z-index: 10;
}//end sticky

} // end wrapper

 .btn{
@include transition;
}

.btn:hover{
//box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.9);
transform: scale(1.05);
}
