
body {
background-image: linear-gradient(135deg, rgba($green-screen, 1), 1%, rgba($green-screen, 1)), url();
} // end body

.main{
grid-column:1 / -1;
grid-row:2;
min-height:73vh;
display:grid;
grid-template-columns:1fr;
grid-template-rows:auto;


@include responsive(small-phone) {
grid-template-columns:1fr 12fr 1fr;
}
@include responsive(phone) {
grid-template-columns:1fr 5fr 1fr;
}

@include responsive(tablet) {
grid-template-columns:1fr 3fr 1fr;

}
@include responsive(laptop) {
grid-template-columns:1fr 3fr 1fr;

}
@include responsive(desktop) {
grid-template-columns:1fr 3fr 1fr;
}
@include responsive(big-desktop) {
grid-template-columns:1fr 3fr 1fr;
}





& > .post {
grid-column:2;
grid-row:1;
width:100%;
//height:100%;
padding:0px 15px 0px 15px;
margin-top:15px;
margin-bottom:15px;
display:grid;
justify-items:center;


&  a.title, &  a.title:hover{
color: #222;
text-decoration: none;
}//title link



& > h1{
color: $color-primary;
margin-top: 1rem;
margin-bottom:4rem;
}



& > .main-content{
width:100%;
display:grid;
justify-items:center;


& > h3{
color: $color-primary;
color:$gwh;
margin-top: 1rem;
margin-bottom:4rem;
}



.p-news {
font-family: $font-news;
font-size: $normal-plus;
color: $color-primary;
color:$gwh;
color:$color-silver-1;
//color: $color-page;
//color:$color-quote-bg;
//color: $color-eggshell;
//color: $color-share-egg;
//color: $color-border-light;






margin-top: 1rem;
margin-bottom:1rem;


@include responsive(tablet){
font-size: $less-big;
}

@include responsive(desktop){
font-size: $med;
}
}//end p-news


& > .mobile-iframe {

width: 80%;
height: 0;
padding-bottom: 56.2%;
position: relative;
}

.mobile-iframe iframe {
position: absolute;
width: 100%;
height: 100%;
}


.img-zoom {
display: block;
margin: auto;
max-width: 100%;
}


.aspectRatioPlaceholder .img-zoom {
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
}

.zoom-effect .img-zoom {
cursor: pointer;
cursor: -webkit-zoom-in;
backface-visibility: hidden;
}

.zoom-effect .zoomImg,
.zoom-effect .zoomImg-wrap {
position: relative;
z-index: 900;
transition: all 300ms;
}

.zoom-effect .zoomImg-wrap--absolute {
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
}

.zoom-effect .zoomImg {
cursor: pointer;
cursor: -webkit-zoom-out;
}

.zoom-effect .zoomOverlay.show {
opacity: 1;
}

.zoom-effect .zoomOverlay {
z-index: 800;
background: #fff;
position: absolute;
transition: opacity 300ms;
opacity: 0;
cursor: pointer;
cursor: -webkit-zoom-out;
}


img-end, p {
margin-block-end: 0;
margin-block-start: 0;
}

.inline-image {
float: right;
margin-left: 10px;
}
.inline-image img {
border-radius: 10px;
max-width: 100%;
height: auto;
}


}//end main content
}//end posts
}//end main

.footer{
grid-row:3 !important;
min-height:13vh !important;
}
