.navigation {
grid-column: col-start 1 / span 12;
grid-row: 1;
display:grid;
grid-template-columns:1fr 1fr 8fr;
grid-template-rows:auto;
background:$color-band;
min-height:7vh;
width:100%;

@include responsive(tablet){
grid-row: 2;
}

@include responsive(tablet){
grid-template-columns:1fr 8fr 1.5fr;
}


& > a.brand{
grid-column:1;
grid-row:1;
display:inline-block;
width:80px;
height:40px;
margin: 0px 10px 0px 10px;
@include responsive(phone){
height:40px;
width:80px;
}

@include responsive(tablet){
//display:none;
}

& > .footer-logo{
background-size:cover;
background-position:center center;
background-repeat:no-repeat;
border-radius:$header-b-rad;
height:100%;
width:100%;
}//end footer logo
}//end brand

& > .hamburger{
grid-column:2;
grid-row:1;
display:block;
border:none;
background:transparent;

& > .fa-bars{
font-size:xx-large;
color:$color-primary;
}

@include responsive(tablet){
display:none;
}
}//end hamburger

& > hamburger:focus, & > button:focus{
outline:none;
}

& > .is-active{
border: 1px solid #fff;
border-radius:$header-b-rad;
}

& > .main-nav{
grid-column:1 / 4;
grid-row:2;
display:none;
justify-self:start;
align-self:center;
width:100%;


@include responsive(tablet){
grid-column:2;
grid-row:1;
justify-self:start;
display:flex;
flex-direction:row;
background-color:transparent;
}

& > ul{
display:flex;
flex-direction:column;
margin-top:15px;


@include responsive(tablet){
flex-direction:row;
margin-top:0px;
}

& > li{
margin-left:0px;

@include responsive(tablet){
}
}//end li
}//end ul
}//end main nav

& > .drop-down{
display:block;
}


& > .phone{
grid-column:3;
grid-row:1;
text-align:right;
align-self:center;
justify-self:end;
width:95%;
display:flex;
justify-content:flex-end;

@include responsive(tablet){

}

& > a{
align-self:center;
}

}//phone

.nav-link {
color:$color-primary;
font-weight:600;
font-size:$large;
font-family:$font-family-ads;
padding: .5rem 1rem;

@include responsive(tablet){
font-size:$normal;
padding: .5rem .7rem;
}

@include responsive(big-tablet){
font-size:$big;
padding: .5rem 1rem;
}
@include responsive(laptop){
}
@include responsive(desktop){
font-size:$med;
}
@include responsive(big-desktop){
font-size:$large;
}

}//end nav-link

.navbar-custom {
background-color: rgba(0,0,0, 0.9);
min-height:7vh;
width:100%;
}

/* for local hrefs */
a.anchor {
display: block;
position: relative;
top: 25px;
visibility: hidden;
}

/* fa bars collapsed navigation toggler */


}//end navigation
