.footer {
grid-column: col-start 1 / span 12;
grid-row:10;
display:grid;
grid-template-columns: 1fr 1fr 1fr;
grid-template-rows:.5fr 2fr;
min-height:150px;
width:100%;
background:$color-band;

@include text-template($large,$font-family-ads, $color-primary);
text-align:center;


& > section{
display:flex;
flex-wrap:wrap;
align-content:center;
justify-content:center;

& > small{
height:auto;
width:100%;
//margin-bottom:15px;

& > a{
@include text-template($small,$font-family-ads, $color-silver-1);
text-decoration:underline;

@include responsive(tablet){
//font-size: $small;
}
}//a
}//end small
}//end sections


& > .hugo{
grid-column:1/4;
grid-row:1;
height:auto;
width:100%;
margin:5px 0px;
text-align:center;
align-self:start;
}//end hugo

& > .footer-center{
align-self:end;
grid-column:2;
grid-row:2;

& > a.develop{
align-self:flex-end;
width:80px;
height:40px;
margin: 0px 10px 0px 10px;
@include responsive(phone){
height:40px;
width:80px;
}
@include responsive(big-tablet){
height:60px;
width:110px;
}

& > .footer-logo{
background-size:cover;
background-position:center center;
background-repeat:no-repeat;
border-radius:$rad-three;
height:100%;
width:100%;
transition: all .2s ease-in-out;
}//end footer logo
}//end develop

& > .develop:hover{
transform: scale(1.2);
}

& > small{
margin:8px 0px;
}
}//end footer-center

& > .footer-right{
grid-column:3;
grid-row:1/3;
align-items:center;

@include responsive(tablet){
text-align:left;
}

& > small > a:hover{
color: $green-screen;
font-size:102%;
}//end hover

}//end footer rigth
}//end footer
